}

.AboutMe {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.AboutMe h2{
  width: 100%;
  text-align: center;
}

.socialMedia {
  display: flex;
  flex-direction: row;
  padding: 10px 0;
